import React, {useState} from 'react';

export default function TextForms(props) {

  const handleupclick=()=>{
      let newText=text.toUpperCase()
      setText(newText);
      if (text.trim().length !== 0){props.showalert("Converted to Uppercase" )}
  }

const handleloclick=()=>{
  let newText=text.toLowerCase()
  setText(newText);
  if (text.trim().length !== 0){props.showalert("Converted to lowercase" )}
}

const handlecopyclick=()=>{
  navigator.clipboard.writeText(text);
  if (text.trim().length !== 0){props.showalert( "Copied to clipboard!" )}
}

const handleclearclick=()=>{
  let newText='';
  setText(newText)
  if (text.trim().length > 0){props.showalert("Cleared all Text!")}
}

const handleextraspace=()=>{
  let newText=text.split(/[ ]+/);
  setText(newText.join(" "))
  if (text.trim().length !== 0){props.showalert("Extra spaces removed!")}
}

const handlecamel=()=>{
  //let newText=text.replace(/([A-Z])/g,' $1');
  let str = text.toLowerCase().split(' ');
  for (var i = 0; i < str.length; i++) {
    str[i] = str[i].charAt(0).toUpperCase() + str[i].slice(1); 
  }
  let newText = str.join(' ');
  setText(newText)
  if (text.trim().length !== 0){props.showalert("Converted to Title Case!")}
}

  const handleonchange=(event)=>{
    setText(event.target.value);
}
  const [text, setText] = useState(' ');
  return (
      <>
    <div className='container' style={{color: props.mode==='dark'?'white':'black'}}>
        <h4>{props.heading}</h4> 
        <div className="mb-3">
           <textarea className="form-control"  value={text} style={{backgroundColor: props.mode==='dark'?'#3d5266':'white', color: props.mode==='dark'?'white':'black'}} onChange={handleonchange} id="mybox" rows="8"></textarea>
        </div>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handleupclick}>UPPERCASE</button>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handleloclick}>lowercase</button>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handlecamel}>TitleCase</button>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handlecopyclick}>Copy</button>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handleclearclick}>Clear</button>
        <button className={`btn btn-${props.btn} mx-2 my-1`} onClick={handleextraspace}>Space</button>
        
    </div>
    <center>
    <div className="container my-5" style={{color: props.mode==='dark'?'white':'black'}}>
        <h3>Your Text Summary</h3>
        <p>{text.trim().length} characters {text.replace(/\n/g, " ").split(' ').filter(value => value !== "").length} words {text.split(". ").length - 1} Sentences</p>
    </div>
    </center>
    </>
  )
}
